import React from 'react'
import mapboxgl from 'mapbox-gl'

mapboxgl.accessToken = 'pk.eyJ1IjoiY210b29tZXkiLCJhIjoiUWQyeTJqMCJ9.MFgvZXmgNt6WzrzP9kJgUA';

class Application extends React.Component {

  constructor(props: Props) {
    super(props);
    this.state = {
      lng: -123.129076,
      lat: 49.291947,
      zoom: 13.2
    };
  }

  componentDidMount() {
    const { lng, lat, zoom } = this.state;

    let count = 5;
    let opacity = 1;
    let width = 6;
    let countIncrement = -1;
    let opacityIncrement = -.1;
    let widthIncrement = 1.5;    

    const timer = () => {
      count = count+countIncrement;
      opacity = opacity+opacityIncrement;      
      width = width+widthIncrement;
      map.setPaintProperty('seawheeze','line-width',width);
      map.setPaintProperty('seawheeze','line-opacity',opacity);
      if (count === 0|| count === 5) {
        countIncrement = -countIncrement;
        opacityIncrement = -opacityIncrement;
        widthIncrement = -widthIncrement;       
      }              
    }

    setInterval(timer, 330);

    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: 'mapbox://styles/cmtoomey/cj814vx9e17aa2slgmyq6adii',
      center: [lng, lat],
      zoom
    });

    map.on('load', () => {
      map.addLayer({
        'id':'seawheeze',
        'type': 'line',
        'source': {
          type: 'vector',
          url: 'mapbox://cmtoomey.28x4rfkc'
        },
        'source-layer':'tracks',
        'paint': {
          'line-width': 4,
          'line-color':'#a09156',
          'line-opacity': 1
        }
      })
    })

    map.on('move', () => {
      const { lng, lat } = map.getCenter();

      this.setState({
        lng: lng.toFixed(4),
        lat: lat.toFixed(4),
        zoom: map.getZoom().toFixed(2)
      });
    });
  }

  render() {

    return (
      <div>
        <div ref={el => this.mapContainer = el} className="absolute top right left bottom" style={{height:'100vh'}}/>
      </div>
    );
  }
}

export default Application


